@import "src/scss/mixins";
@import "src/scss/variables";

.textBlock {
  position: relative;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 690px;
}

.textWrapper {
  margin-bottom: 90px;
}

.text {
  font: $regular $font-size-secondary $font-family-lore;
  line-height: 1.2;
  text-indent: 30px;

  &:not(:last-child) {
    margin-bottom: 50px;
  }
}