@import "src/scss/mixins";
@import "src/scss/variables";

.wrapper {
  @include card-bg;
  display: flex;
  flex-direction: column;
  height: 451px;
}

.image {
  width: 100%;
  padding: 8px 8px 0 8px;
  border-radius: 26px;
}

.content {
  padding: 24px 24px 32px;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.title {
  margin-bottom: 16px;
  font-weight: $bold;
  font-size: 24px;
  letter-spacing: -1px;
}

.description {
  margin-bottom: 24px;
  font-weight: $medium;
  font-size: 14px;
  line-height: 1.4;

  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.button {
  display: flex;
  align-items: center;
  margin-top: auto;
  color: $text-yellow;

  &:hover {
    text-decoration: underline;
  }
}