.footer {
  width: 100vw;
  padding: 20px 40px;
  //display: flex;
  //align-items: center;
}

.top {
  padding: 30px 0;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #505828;
}

.logo {
  margin-right: 65px;
}

.navigationList {
  column-count: 3;
}

.navigationItem {
  margin-right: 56px;
  &:nth-child(odd) {
    margin-bottom: 16px;
  }
}

.bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 33px 0 45px;
}

.rights {
  display: flex;
  align-items: center;
  width: 50%;
}

.year {
  margin-right: 12%;
}

.followUs {
  display: flex;
  justify-content: flex-end;
  width: 50%;
  align-items: center;
}

.followUsText {
  margin-right: 44px;
}